#root {
  text-align: center;
  display: flex;
  justify-content: center;
  width: 100vw;
  min-height: 100vh;
  overflow: hidden;
  /* background: linear-gradient(#fff, #999); */
  background: linear-gradient(#000000, #c6d3ff);
}

#idea {
  display: flex;
  justify-content: center;
  width: 100vw;
  min-height: 100vh;
  overflow: hidden;
  background: linear-gradient(#000000, #c6d3ff);
}

#root > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.test > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.row {
  flex-direction: row !important;
}

.row > * {
  margin: 5px;
}

h1 {
  /* font-family: "Damion", cursive; */
  color: #fff;
  text-shadow: 0px 0px 50px 0px rgba(0, 0, 0, 0.3);
}

h2 {
  color: #fff;
}

.swipe {
  position: absolute;
}

.cardContainer {
  width: 90vw;
  max-width: 260px;
  height: 500px;
}

.card {
  position: relative;
  left: 20px;
  bottom: 40px;
  background-color: #fff;
  /* width: 80vw; */
  width: 800px;
  height: 460px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.3);
  border-radius: 20px;
  background-size: cover;
  background-position: center;
}

.cardContent {
  width: 100%;
  height: 100%;
}

.cardImagediv {
  width: 100%;
  height: 150px;
  border-bottom: 1px solid #ccc;
  overflow: auto;
}

.cardImage {
  width: 130px;
  height: 130px;
  border-style: solid;
  margin: 10px 10px 20px 20px;
  padding: 10px 10px 10px 10px;
}

.cardImagediv::-webkit-scrollbar {
  display: none;
}

.card_h3 {
  position: relative;
  /* bottom: 0; */
  /* margin: 10px; */
  color: #fff;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  height: 35px;
  background-color: rgb(0, 0, 0);
  text-align: center;
  vertical-align: middle;
}

.para {
  margin: 10px 20px 10px 20px;
}

.infoText {
  width: 100%;
  height: -28px;
  justify-content: center;
  display: block;
  color: #000;
  animation-name: popup;
  animation-duration: 800ms;
  position: absolute;
  bottom: 0px;
  left: 0px;
}


.buttons {
  margin: 20px;
  display: flex;
}

.buttons button {
  flex-shrink: 0;
  padding: 10px;
  border-radius: 5px;
  border: none;
  color: #fff;
  font-size: 18px;
  background-color: #9198e5;
  transition: 200ms;
  margin: 0 10px;
  font-weight: bolder;
  width: 160px;
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.1);
}

.buttons button:hover {
  transform: scale(1.05);
}

@keyframes popup {
  0% {
    transform: scale(1, 1);
  }
  10% {
    transform: scale(1.1, 1.1);
  }
  30% {
    transform: scale(0.9, 0.9);
  }
  50% {
    transform: scale(1, 1);
  }
  57% {
    transform: scale(1, 1);
  }
  64% {
    transform: scale(1, 1);
  }
  100% {
    transform: scale(1, 1);
  }
}
